.msl-search-list-input{margin-bottom:10px}.msl-searchable-list{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}.msl-searchable-list__items{border:1px solid #5b5d66;flex-basis:100%;overflow-y:auto;overflow-x:hidden}.msl-searchable-list__no-item{font-size:.9em;padding:5px 10px;color:#c0c2c6}.msl-searchable-list__item{font-size:.9em;padding:5px 10px;cursor:pointer;border-bottom:1px solid #252b45}.msl-searchable-list__item:hover{background:#3858e7;color:#fff}.msl-multi-select{display:-webkit-inline-box;display:inline-flex;height:300px}.msl-multi-select__actions{display:-webkit-box;display:flex;-webkit-box-pack:center;justify-content:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;-webkit-box-align:center;align-items:center;padding:0 10px}.msl-multi-select__action{cursor:pointer}.msl-multi-select__action:hover{color:#c0c2c6}.msl-multi-select__action-select-all{margin-bottom:20px}.msl-multi-select__action-unselect-all{margin-top:20px}.msl-multi-select__action-icon{margin-top:17px;color:#919191}.msl-multi-select__list{width:100%}